$--font-dm-sans: "DM Sans";
$--font-sf-pro: "Outfit";

/* font sizes */
$--font-size-base: 16px;
$--font-size-5xl: 24px;
$--font-size-13xl: 32px;
$--font-size-mini: 15px;
$--font-size-29xl: 48px;

/* Colors */
$--color-white: #fff;
$--color-black: #000;
$--color-gray-100: rgba(255, 255, 255, 0.5);
$--color-darkslategray-100: #404040;
$--color-darkslategray-200: rgba(64, 64, 64, 0.5);
$--color-cornflowerblue: #3d92ed;
$--color-lightpink: #ffb6c1;
$--color-burlywood: #deb887;

/* Border radiuses */
$--br-81xl: 100px;

.icon {
  width: 70%;
  height: 726px;
  object-fit: cover;
}

.ellipse_div,
.frame_inner {
  position: absolute;
  top: 50%;
  right: 80%;
  border-radius: 50%;
  background-color: $--color-lightpink;
  filter: blur(280px);
  width: 50%;
  height: 30%;
}

.ellipse_div {
  top: 0;
  left: 70%;
  // margin-left: 300px;
  background-color: $--color-burlywood;
}

.grid {
  margin-bottom: 60px;
}

.a_personalized_assessment,
.why_partner_with1 {
  position: absolute;
  top: 91px;
  left: calc(50% - 332px);
  font-size: $--font-size-29xl;
  line-height: 98.34%;
  display: none;
  color: $--color-cornflowerblue;
}

.a_personalized_assessment {
  top: 148px;
  left: calc(50% - 346px);
  font-size: $--font-size-5xl;
  color: $--color-darkslategray-100;
  text-align: center;
}

.custom_assessments,
.delve_deeper_into {
  position: absolute;
  left: calc(50% - 165px);
  line-height: 98.34%;
}

.delve_deeper_into {
  top: 40px;
  display: inline-block;
  width: 330px;
}

.custom_assessments {
  top: 0;
  font-size: $--font-size-13xl;
  color: $--color-white;
}

.a_comprehensive_look_into_our_parent,
.delve_deeper_into_our_speciali_parent {
  position: absolute;
  top: 694px;
  left: calc(50% - 573px);
  width: 330px;
  height: 85px;
}

.a_comprehensive_look_into_our_parent {
  left: calc(50% - 164px);
}

.showcase_how_your {
  position: absolute;
  top: 72px;
  left: calc(50% - 165px);
  line-height: 98.34%;
  display: inline-block;
  width: 330px;
}

.co_branding {
  margin: 0;
}

.showcase_how_your_organization_parent {
  position: absolute;
  top: 694px;
  left: calc(50% + 244px);
