  height: 117px;
}

.group {
  // top: 1510px;
  // left: calc(50% - 955px);
  display: flex;
  margin-bottom: 100px;
  justify-content: center;
  position: relative;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  background-color: #000;
  width: 100%;
  height: 882px;
  gap: 20px;
  padding: 20px;
  text-align: left;
  font-size: $--font-size-mini;
  color: $--color-gray-100;
  font-family: $--font-sf-pro;
}