$--font-dm-sans: "DM Sans";
$--font-sf-pro: "Outfit";
/* font sizes */
$--font-size-base: 16px;
$--font-size-5xl: 24px;
$--font-size-13xl: 32px;
$--font-size-mini: 15px;
$--font-size-29xl: 48px;

/* Colors */
$--color-white: #fff;
$--color-black: #000;
$--color-gray-100: rgba(255, 255, 255, 0.5);
$--color-darkslategray-100: #404040;
$--color-darkslategray-200: rgba(64, 64, 64, 0.5);
$--color-cornflowerblue: #3d92ed;
$--color-palegreen: #98fb98;
$--color-darkturquoise: #00ced1;

/* Border radiuses */
$--br-81xl: 100px;

.ellipse_parent {
  background: linear-gradient(#fff, #fff), linear-gradient(#e3e8e9, #e3e8e9),
    linear-gradient(90deg, rgba(63, 212, 238, 0.1), rgba(75, 155, 238, 0.1));
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  font-size: $--font-size-29xl;
  color: $--color-cornflowerblue;
  font-family: "Outfit";
  margin-bottom: 100px;

  @media screen and (max-width: 650px) {
    display: block;
  }
}

.mainText {
  text-align: center;
  font-size: 38px;
  font-weight: 700;
  margin-top: 70px;
  font-family: "Outfit";
}

.frame_child,
.frame_item {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  background-color: $--color-palegreen;
  filter: blur(280px);
  width: 50%;
  height: 30%;
}

.frame_item {
  top: 0;
  left: 70%;
  background-color: $--color-palegreen;
}

.title {
  text-align: justify;
  font-size: 32px;
  font-weight: 700;
  line-height: 31.47px;
  margin-bottom: 10px;
  font-family: "Outfit";
  color: #404040;
}

.subtitle {
  text-align: left;
  font-size: 15px;
  font-weight: 400;
  line-height: 14.75px;
}