$--font-dm-sans: "DM Sans";
$--font-sf-pro: "Outfit";

/* font sizes */
$--font-size-base: 16px;
$--font-size-5xl: 24px;
$--font-size-13xl: 32px;
$--font-size-mini: 15px;
$--font-size-29xl: 48px;

/* Colors */
$--color-white: #fff;
$--color-black: #000;
$--color-gray-100: rgba(255, 255, 255, 0.5);
$--color-darkslategray-100: #404040;
$--color-darkslategray-200: rgba(64, 64, 64, 0.5);
$--color-cornflowerblue: #3d92ed;

/* Border radiuses */
$--br-81xl: 100px;

.transform_career_paths {
  margin: 0;
  font-size: 55px;
  font-family: "Outfit";
  font-weight: 700;
  line-height: 58.67px;
}

.bespoke_solutions_for,
.transform_career_paths_container {
  position: relative;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.bespoke_solutions_for {
  font-size: 18px;
  text-align: center;
}

.transform_career_paths_with_ca_parent {
  position: relative;
  width: 100%;
  height: 700px;
  overflow: hidden;
  background-image: url(../../assets/images/org-home/frame1.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: $--font-size-29xl;
  color: $--color-white;
  font-family: "Outfit";

  .maintest {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 104px;
    height: 100%;
    flex-direction: column;
    gap: 20px;
  }
}

@media screen and (max-width: 650px) {
  .transform_career_paths {
    font-size: 50px;
    line-height: 48.67px;
  }
}

@media screen and (max-width: 450px) {
  .transform_career_paths {
    font-size: 40px;
    line-height: 38.67px;
  }
}

@media screen and (max-width: 280px) {
  .transform_career_paths {
    font-size: 30px;
    line-height: 38.67px;
  }
}